import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpService } from 'src/app/Services/http.service';
import {DataService} from 'src/app/Services/data.service';
import {DialogService} from 'src/app/Services/dialog.service';
import { invalid } from '@angular/compiler/src/render3/view/util';

@Component({
  selector: 'app-register-page',
  templateUrl: './register-page.component.html',
  styleUrls: ['./register-page.component.css']
})
export class RegisterPageComponent implements OnInit {
  titles: any;
  profileForm = new FormGroup({
    firstName: new FormControl('', [Validators.required, Validators.maxLength(16)]),
    middleName: new FormControl('', [Validators.maxLength(16)]),
    lastName: new FormControl('', [Validators.required, Validators.maxLength(16)]),
    email: new FormControl('', [Validators.required, Validators.email, Validators.maxLength(45)]),
    password: new FormControl('', [Validators.required, Validators.maxLength(16), Validators.minLength(8)]),
    confirmPassword: new FormControl('', [Validators.required]),
    title: new FormControl('', [Validators.required]),
    type: new FormControl('1', [Validators.required]),
  });

  constructor(
    public router: Router,
    public httpService: HttpService,
    public dataService:DataService,
    public dialogService: DialogService

  ) {

  }

  ngOnInit() {
    this.titles = ["Mr", "Mrs", "Miss"]
  }
  doRegister(event) {
    if(this.profileForm.controls.password.value!=this.profileForm.controls.confirmPassword.value){
      this.dialogService.openDialog("Invalid Form", "Passwords does not match");
      return false;
    }
    this.dataService.showSpinner();
    console.log(this.profileForm);
    var regData = {
      email: this.profileForm.controls.email.value,
      password: this.profileForm.controls.password.value,
      firstName: this.profileForm.controls.firstName.value,
      middleName: this.profileForm.controls.middleName.value,
      lastName: this.profileForm.controls.lastName.value,
      title: this.profileForm.controls.title.value,
      type: this.profileForm.controls.type.value,

    }
    this.httpService.getUsers().subscribe(
      (users: Array<any>) => {
        var userExist = this.isUserExist(users, regData.email);
        if (userExist) {
          this.dataService.hideSpinner();
          this.dialogService.openDialog("User exist", "User with the given mail id already exist");
        }
        else {
          this.httpService.saveUser(users, regData).subscribe(
            (data) => {
              this.dataService.hideSpinner();
              this.dataService.setUserData(regData);
              this.router.navigate(["home"]);
            },
            (error)=>{
              this.dataService.hideSpinner();
              this.dialogService.openDialog("Error", "Failed to connect to server");
            })
        }

      },
      (error) => {
        this.dataService.hideSpinner();
        this.dialogService.openDialog("Error", "Failed to connect to server");
      }
    );
  }
  doLogin(event) {
    this.router.navigate(["login"]);
  }
  isRegFormValid() {
    return false;
  }
  isUserExist(users: Array<any>, email: String) {
    if(users){
      for (var i = 0; i < users.length; i++) {
        if (users[0].email == email) {
          return true;
        }
      }
    }
    return false;
  }

}
