import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import { FormGroup,FormControl,Validators } from '@angular/forms';
import {HttpService} from 'src/app/Services/http.service';
import { DataService } from 'src/app/Services/data.service';
import { DialogService } from 'src/app/Services/dialog.service'

@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.css']
})
export class LoginPageComponent implements OnInit {

  loginForm = new FormGroup({
    email: new FormControl('',[Validators.required, Validators.maxLength(45),Validators.minLength(1)]),
    password: new FormControl('',[Validators.required,Validators.maxLength(16),Validators.minLength(1)])
  });

  constructor(
    public router:Router,
    public httpService:HttpService,
    public dataService:DataService,
    public dialog:DialogService
    ) { }

  ngOnInit() {
  }
  doLogin(event){
    this.dataService.showSpinner();
    var user={
      email:this.loginForm.controls.email.value,
      password:this.loginForm.controls.password.value
    }
    this.httpService.getUsers().subscribe(
    (users)=>{
      this.dataService.hideSpinner()
      if(this.isValidUser(users,user)){
        console.log("Login successful");
        this.router.navigate(["home"]);
      }
      else{
        this.dialog.openDialog('Login Failed','Invalid username or password');
      }
    },
    (error)=>{
      this.dataService.hideSpinner();
      console.log(error);
    });
  }
  doRegister(event){
    this.router.navigate(["/register"]);
  }
  isValidUser(users,user){
    if(users){
      for(var i=0;i<users.length;i++){
        if(users[i].email==user.email&&users[i].password==user.password){
          this.dataService.setUserData(users[i]);
          return true;
        }
      }
    }
    return false;
  }
}
