import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/Services/http.service';
import { DataService } from 'src/app/Services/data.service';
import { DialogService } from 'src/app/Services/dialog.service';

@Component({
  selector: 'app-academics-page',
  templateUrl: './academics-page.component.html',
  styleUrls: ['./academics-page.component.css']
})
export class AcademicsPageComponent implements OnInit {
  posts: any;
  myPosts: any;
  loading: boolean;
  constructor(public http: HttpService,
    public dataService: DataService,
    public dialogService: DialogService) { }
  ngOnInit() {
    this.getPosts();
  }
  getPosts() {
    this.loading = true;
    this.http.getPosts('academics').subscribe(
      (posts) => {
        this.posts = posts;
        this.myPosts = [];
        this.getMyPosts(posts);
        this.loading = false;
      },
      (error) => {
        this.dialogService.openDialog("Failure", "Failed to contact server please try again");
      }
    )
  }
  onPostCreated(post) {
    this.loading = true;
    this.http.getPosts('academics').subscribe(
      (posts: Array<any>) => {
        var newPost = {
          "createdBy": this.dataService.userData.email,
          "createrName": this.dataService.userData.firstName + " " + this.dataService.userData.lastName,
          "createdOn": new Date().toLocaleDateString() + " " + new Date().toLocaleTimeString(),
          "content": post,
          "image": null,
          "likes": 0,
          "comments": []
        }
        if (posts == null || posts == undefined) {
          posts = [];
        }
        posts.unshift(newPost);
        this.http.setPosts('academics', posts).subscribe(
          () => {
            this.dialogService.openDialog("Success", "Post created successfully");
            this.loading = false;
          },
          (error) => {
            this.dialogService.openDialog("Failure", "Failed to contact server please try again");
          }
        )
      })
  }
  getMyPosts(posts) {
    if (posts && posts.length > 0) {
      for (var i = 0; i < posts.length; i++) {
        if (posts[i].createdBy == this.dataService.userData.email) {
          if (!this.myPosts) {
            this.myPosts = [];
          }
          this.myPosts.push(posts[i])
        }
      }
    }
  }
  onDeletePost(post) {
    this.http.getPosts('academics').subscribe(
      (posts: Array<any>) => {
        if (posts) {
          for (var i = 0; i < posts.length; i++) {
            if (JSON.stringify(posts[i]) == JSON.stringify(post)) {
              posts.splice(i, 1);
              this.updatePosts(posts,"Post deleted successfully");
              return;
            }
          }
        }
      }
    )
  }
  onEditPost(data) {
    this.http.getPosts('academics').subscribe(
      (posts: Array<any>) => {
        if (posts) {
          for (var i = 0; i < posts.length; i++) {
            if (JSON.stringify(posts[i]) == JSON.stringify(data.post)) {
              posts[i].content = data.content;
              this.updatePosts(posts,"Post edited successfully");
              return;
            }
          }
        }
      }
    )
  }
  onPostCommented(data){
    this.http.getPosts('academics').subscribe(
      (posts: Array<any>) => {
        if (posts) {
          for (var i = 0; i < posts.length; i++) {
            if (JSON.stringify(posts[i]) == JSON.stringify(data.post)) {
              if(!posts[i].comments)
              {
                posts[i].comments=[];
              }
              var comment={
                "name":this.dataService.userData.firstName + " " + this.dataService.userData.lastName,
                "comment":data.comment,
                "email":this.dataService.userData.email
              }
              posts[i].comments.unshift(comment);
              this.updatePosts(posts,"Comment added successfully");
              return;
            }
          }
        }
      }
    )
  }
  onPostLiked(data) {
    this.http.getPosts('academics').subscribe(
      (posts: Array<any>) => {
        if (posts) {
          for (var i = 0; i < posts.length; i++) {
            if (JSON.stringify(posts[i]) == JSON.stringify(data.post)) {
              var likes: any;
              if (posts[i].likes) {
                likes = posts[i].likes;
                var liked = false;
                for (var j = 0; j < likes.length; j++) {
                  if (likes[j].email == this.dataService.userData.email) {
                    likes.splice(j, 1);
                    liked = true;
                    posts[i].likes = likes;
                    this.updatePosts(posts,"Post unliked successfully");
                  }
                }
                if (!liked) {
                  var like = {
                    email: this.dataService.userData.email,
                    name: this.dataService.userData.firstName + " " + this.dataService.userData.lastName
                  }
                  likes.unshift(like);
                  posts[i].likes = likes;
                  this.updatePosts(posts,"Post liked successfully");
                }
              }
              else {
                likes = [];
                var like = {
                  email: this.dataService.userData.email,
                  name: this.dataService.userData.firstName + " " + this.dataService.userData.lastName
                }
                likes.unshift(like);
                posts[i].likes = likes;
                this.updatePosts(posts,"Post liked successfully");

              }
              return;
            }
          }
        }
      }
    )
  }
  updatePosts(posts,message) {
    this.loading = true;
    this.http.setPosts('academics', posts).subscribe(
      () => {
        this.dialogService.openDialog("Success", message);
        this.loading = false;
        this.getPosts();
      },
      (error) => {
        this.dialogService.openDialog("Failure", "Failed to contact server please try again");
        this.loading = false;
      }
    )
  }
}
