import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MatInputModule} from '@angular/material/input';
import {MatButtonModule} from '@angular/material/button';
import {MatSelectModule} from '@angular/material/select';
import {MatRadioModule} from '@angular/material/radio';
import {MatMenuModule} from '@angular/material/menu';
import {MatIconModule} from '@angular/material/icon';
import {MatListModule} from '@angular/material/list';
import {MatTabsModule} from '@angular/material/tabs';
import {MatCardModule} from '@angular/material/card';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import {MatDialogModule} from '@angular/material/dialog';
import {MatProgressSpinnerModule} from '@angular/material/progress-spinner';

import {LoginPageComponent} from './Pages/login-page/login-page.component';
import { HomePageComponent } from './Pages/home-page/home-page.component';
import { RegisterPageComponent } from './Pages/register-page/register-page.component';
import { AcademicsPageComponent } from './Pages/home-page/academics-page/academics-page.component';
import { CareersPageComponent } from './Pages/home-page/careers-page/careers-page.component';
import { ImmigrationComponent } from './Pages/home-page/immigration/immigration.component';
import { SocialComponent } from './Pages/home-page/social/social.component';

import { HeaderComponent } from './Components/header/header.component';
import { PostComponent } from './Components/post/post.component';
import { CreatePostComponent } from './Components/create-post/create-post.component';
import { DialogComponent } from './Components/dialog/dialog.component';

const routes: Routes = [
  { path: 'login', component: LoginPageComponent },
  { path: 'register', component: RegisterPageComponent },
  { path: 'home', component: HomePageComponent,
      children: [
        { path: '', redirectTo: 'academics', pathMatch: 'full' },
        { path: 'academics', component: AcademicsPageComponent },
        { path: 'careers', component: CareersPageComponent },
        { path: 'immigration', component: ImmigrationComponent },
        { path: 'social', component: SocialComponent }
      ] },
  {path: '', redirectTo: '/login', pathMatch: 'full'}
];

@NgModule({
  declarations: [
    LoginPageComponent,
    HomePageComponent,
    RegisterPageComponent,
    AcademicsPageComponent,
    CareersPageComponent,
    HeaderComponent,
    PostComponent,
    CreatePostComponent,
    ImmigrationComponent,
    SocialComponent,
    DialogComponent
  ],
  imports: [
    RouterModule.forRoot(routes),
    BrowserAnimationsModule,
    MatInputModule,
    MatButtonModule,
    MatSelectModule,
    MatRadioModule,
    MatMenuModule,
    MatIconModule,
    MatListModule,
    MatTabsModule,
    MatCardModule,
    ReactiveFormsModule,
    FormsModule,
    HttpClientModule,
    MatDialogModule,
    MatProgressSpinnerModule
  ],
  entryComponents: [
    DialogComponent
  ],
  exports: [RouterModule,
    MatProgressSpinnerModule]
})
export class AppRoutingModule { }
